import { useEffect, useState } from 'react';
import ReactDOM from 'react-dom/client';
import Navbar from './components/Navbar';
import React from 'react';

import RegisterForm from "./components/RegisterForm"
import task from './taksClass';
import MainPage from './components/MainPage';
import Test from './components/Test';
import Footer from './components/Footer';






let stock =  window.localStorage


function App() {
  let [storage , setStorage] = useState(stock)
  
  window.changeStorage = (property,value)=>{
    setStorage((prev)=>{
      if(typeof value === "string"){
        stock = Object.assign(stock,{[property]:value})
      }
      else{
        stock = Object.assign(stock,{[property]:JSON.stringify(value)})
      }

      return Object.assign({},stock)
    })


  }
  window.getStorage = (value)=>{
    let ourValue;
    try {
      ourValue = JSON.parse(storage[value])
    } catch (error) {
      ourValue = storage[value]
    }
    return ourValue
  }

  if (getStorage("init") == undefined) {
    initStorage()
  }

  useEffect(()=>{
    const tooltipTriggerList = document.querySelectorAll('[data-bs-toggle="tooltip"]')
    const tooltipList = [...tooltipTriggerList].forEach(tooltipTriggerEl => new bootstrap.Tooltip(tooltipTriggerEl))
  },[])


if(getStorage("registered") == true){
  return (
    <>
    <Navbar />
    <MainPage storage={storage} />
    <Footer />
    </>
    ); 
}
  return (
    <div className='pre-register-container'>
      <RegisterForm storage={storage}/>
      <Footer />
      {/* <Test /> */}
    </div> 
  )


  function initStorage() {
    changeStorage("days" , [])
    changeStorage("tasks" , [
      new task("صلاة كل ألأوقات",true),
      new task("(صفحتين) قرائة القرآن",true),

      new task("عدم التدخين",false),
      new task("عدم النضر إلى المحرمات عن قصد",false),

      new task("صلاة واحد مع ألجماعة",null),
      new task("حمد الله على نعمه",null),
      new task("صلاة ألفجر",null),
    ])

    changeStorage("init" , true)
  }
  
}



const root = ReactDOM.createRoot(document.querySelector("#root"))
root.render(<App />)


